import IonIcon from "@reacticons/ionicons";
import { doc, getDoc } from "firebase/firestore";
import { useEffect, useState } from "react";
import { toast } from "react-toastify";
import Button from "../../components/Button";
import Loading from "../../components/shared/Loading";
import { db } from "../../configs/firebase.config";
import { useAppDispatch, useAppSelector } from "../../hooks/useRedux";
import { authUpdateImage } from "../../redux/reducers/auth";
import history from "../../redux/store/history";

const Accuracy = () => {

  const [account, setAccount] = useState<any>(null)
  const uid = useAppSelector(state => state.auth.uid)

  const dispatch = useAppDispatch()
  const loading = useAppSelector(state => state.auth.isLoading)

  const [isImageUrl, setIsImageUrl] = useState<any>(null)
  const [isImageFaceUrl, setIsImageFaceUrl] = useState<any>(null)
  const [isImageBackUrl, setIsImageBackUrl] = useState<any>(null)

  const handleUpdateImageUrl = () => {

    if (isImageUrl && isImageFaceUrl && isImageBackUrl) {
      dispatch(authUpdateImage({
        imageUrlFace: isImageUrl,
        imageUrlBack: isImageBackUrl,
        imageUrlFront: isImageFaceUrl,
      }))
    } else {
      if (account?.image?.imageUrlFace && account?.image?.imageUrlBack && account?.image?.imageUrlFront) {
        history.push('/user/infomation-confirm')
      } else {
        toast.error('Vui lòng chọn 3 ảnh')
      }
    }
  }

  const getAccount = async () => {
    const docRef = doc(db, "users", uid);
    const docSnap = await getDoc(docRef);

    if (docSnap.exists()) {
      setAccount(docSnap.data());
    }
  }

  useEffect(() => {
    getAccount()
  }, [])

  return (
    <div className="p-4">
      {
        loading && <Loading />
      }
      <h1 className="py-[10px] text-center font-bold text-[18px]">
        Chụp ảnh định danh KYC
      </h1>
      <div className=" flex flex-col gap-4">
        {
          !account?.image?.imageUrlFront && (
            <input type="file" accept="image/*" hidden name="IdentificationCardFront" onChange={(e: any) => {
              if (e.target.files[0]) {
                setIsImageFaceUrl(e.target.files[0])
              }
            }}
              id='IdentificationCardFront'
            />
          )
        }

        <label htmlFor="IdentificationCardFront" className="h-[20vh] overflow-hidden flex-col gap-2 cursor-pointer border bg-[#e9e9e9] border-dashed rounded-lg border-[#a0a0a0] flex justify-center items-center">
          {
            isImageFaceUrl ? (
              <img src={
                URL.createObjectURL(isImageFaceUrl)
              } alt="nev"
                className='h-full object-cover'
              />
            ) : (
              <>
                {
                  account?.image?.imageUrlFront ? (
                    <img src={
                      account?.image?.imageUrlFront
                    } alt="nev"
                      className='h-full object-cover'
                    />
                  ) : (
                    <>
                      <IonIcon name="camera" className="text-[#a0a0a0] text-[30px]" />
                      <p className="text-[#a0a0a0]">Mặt trước CCCD</p>
                    </>
                  )
                }
              </>
            )
          }
        </label>

        {
          !account?.image?.imageUrlBack && (
            <input type="file" accept="image/*" hidden name="BackOfIdentificationCard" onChange={(e: any) => {
              if (e.target.files[0]) {
                setIsImageBackUrl(e.target.files[0])
              }
            }}
              id='BackOfIdentificationCard'
            />
          )
        }

        <label htmlFor="BackOfIdentificationCard" className="h-[20vh] overflow-hidden flex-col gap-2 cursor-pointer border bg-[#e9e9e9] border-dashed rounded-lg border-[#a0a0a0] flex justify-center items-center">
          {
            isImageBackUrl ? (
              <img src={
                URL.createObjectURL(isImageBackUrl)
              } alt="nev"
                className=' h-full object-cover'
              />
            ) : (
              <>
                {
                  account?.image?.imageUrlBack ? (
                    <img src={
                      account?.image?.imageUrlBack
                    } alt="nev"
                      className='h-full object-cover'
                    />
                  ) : (
                    <>
                      <IonIcon name="camera" className="text-[#a0a0a0] text-[30px]" />
                      <p className="text-[#a0a0a0]">Mặt sau CCCD</p>
                    </>
                  )
                }

              </>
            )
          }
        </label>


        {
          !account?.photoURL && (
            <input type="file" accept="image/*" hidden name="avatar" onChange={(e: any) => {
              if (e.target.files[0]) {
                setIsImageUrl(e.target.files[0])
              }
            }}
              id='avatar'
            />
          )
        }
        <label htmlFor="avatar" className="h-[20vh] flex-col overflow-hidden cursor-pointer border bg-[#e9e9e9] border-dashed rounded-lg border-[#a0a0a0] flex justify-center items-center">
          {
            isImageUrl ? (
              <img src={
                URL.createObjectURL(isImageUrl)
              } alt="nev"
                className='h-full object-cover'
              />
            ) : (
              <>
                {
                  account?.photoURL ? (
                    <img src={
                      account?.photoURL
                    } alt="nev"
                      className='h-full object-cover'
                    />
                  ) : (
                    <>
                      <IonIcon name="camera" className="text-[#a0a0a0] text-[30px]" />
                      <p className="text-[#a0a0a0]">Ảnh chân dung</p>
                    </>
                  )
                }
              </>
            )
          }
        </label>
        <Button
          kind="primary"
          className="mt-[20px] text-[16px] rounded-[50px] duration-300 border-white hover:bg-white hover:text-[#00519c] hover:border-[#00519c] hover:border-[1px] h-[40px] w-[90px] m-auto"
          onClick={() => handleUpdateImageUrl()}
        >
          Tiếp tục
        </Button>
      </div>
    </div >
  );
};

export default Accuracy;
